@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  h1 {
    @apply font-outfit font-extrabold text-5xl text-center text-darkPurple;
  }
  h2 {
    @apply font-outfit font-bold text-4xl underline decoration-solid text-center text-darkPurple;
  }
  h3 {
    @apply font-outfit font-semibold text-2xl tracking-wide text-center text-darkPurple;
  }
  h4 {
    @apply font-nunito font-semibold text-xl underline decoration-solid text-center text-darkPurple;
  }
  h5 {
    @apply font-nunito font-semibold text-lg tracking-wide text-center text-darkPurple;
  }
  p {
    @apply font-nunito font-normal text-sm text-black leading-7;
  }
  .base-container {
    @apply w-full flex flex-col justify-center items-center gap-10;
  }
  .page-header-spaced {
    @apply w-full h-[65vh] flex flex-col justify-center items-center bg-gradient-to-b from-purple to-white;
  }
  .menu-item {
    @apply font-nunito font-normal text-base text-darkPurple hover:font-bold hover:cursor-pointer hover:underline;
  }
  .menu-item-disabled {
    @apply menu-item opacity-50 pointer-events-none;
  }
  .project-card {
    @apply flex flex-col justify-start items-start border-2 border-darkPurple bg-lightPurple w-full sm:basis-1/2 md:basis-1/3 p-5 gap-3 hover:cursor-pointer hover:scale-105 hover:ring-4 hover:ring-purple hover:z-10 transition-all;
  }
  .project-card-disabled {
    @apply opacity-50 pointer-events-none;
  }
  .project-card-img {
    @apply object-cover w-full border-2 border-darkPurple;
  }
  .project-card-date {
    @apply text-sm font-semibold bg-darkPurple text-white px-2 rounded-full hover:cursor-crosshair;
  }
  .project-card-topic-container {
    @apply flex flex-wrap w-full justify-start items-center gap-1;
  }
  .project-card-topic {
    @apply text-sm font-medium bg-white text-darkPurple px-2 rounded-full border border-darkPurple hover:cursor-crosshair;
  }
  .project-card p {
    @apply text-sm;
  }
  .feature-card {
    @apply flex flex-col justify-center items-center gap-5;
  }
  .feature-card-fade-box {
    @apply w-full md:w-1/3 border-2 border-darkPurple bg-lightPurple p-6;
  }
  .feature-card-text {
    @apply text-center max-w-[50vw] md:max-w-[25vw];
  }
  .accordian-tab {
    @apply flex flex-col justify-start items-center gap-2 text-darkPurple border-2 border-darkPurple hover:ring-4 hover:ring-purple hover:cursor-pointer hover:z-10 p-3 transition-all hover:scale-105;
  }
  .accordian-tab-active {
    @apply flex flex-col justify-start items-center gap-2 text-darkPurple border-2 border-darkPurple p-3 bg-purple;
  }
  .accordian-tab-mobile {
    @apply flex flex-row w-full justify-between items-center p-2 border-2 border-darkPurple bg-lightPurple text-darkPurple hover:cursor-pointer transition-all;
  }
  .accordian-tab-active-mobile {
    @apply flex flex-row w-full justify-between items-center p-2 border-2 border-darkPurple bg-purple text-darkPurple hover:cursor-pointer;
  }
  .accordian-content-mobile {
    @apply w-full flex flex-col md:flex-row justify-start items-start gap-2 p-4 bg-white border-2 border-darkPurple transition-all;
  }
  .process-content {
    @apply w-full flex flex-col md:flex-row justify-center items-center gap-5;
  }
  .medipin-image-thumbnail {
    @apply md:max-w-[40vw] border-2 md:border-4 border-darkPurple;
  }
  .medipin-document-link {
    @apply flex flex-row md:flex-col justify-center items-center gap-4  hover:ring-4 hover:ring-purple hover:scale-105 p-3 text-darkPurple text-center transition-all;
  }
  .medipin-document-icon {
    @apply text-2xl md:text-4xl;
  }

  /* cv styles */

  .cv-school-container {
    @apply flex flex-col sm:flex-row md:flex-col lg:flex-row justify-center items-stretch border-2 border-darkPurple md:w-1/2;
  }
  .cv-school-logo {
    @apply w-full flex justify-center items-center bg-purple p-10;
  }
  .cv-school-info-container {
    @apply flex flex-col justify-center md:justify-start items-center md:items-start gap-3 p-4;
  }
  .cv-school-name {
    @apply text-left text-xl md:text-2xl;
  }
  .cv-school-diploma {
    @apply text-left text-lg md:text-xl;
  }

  /* process question frames */
  .process-question {
    @apply underline font-bold text-darkPurple underline-offset-4 hover:cursor-crosshair;
  }
  .process-outcome-badge {
    @apply italic font-bold text-darkPurple hover:cursor-crosshair;
  }
}

@media screen(md) {
  h1 {
    @apply font-outfit font-extrabold text-8xl text-center;
  }
  h2 {
    @apply font-outfit font-bold text-6xl underline decoration-solid text-center;
  }
  h3 {
    @apply font-outfit font-semibold text-4xl tracking-wide text-center;
  }
  h4 {
    @apply font-nunito font-semibold text-2xl underline decoration-solid text-center;
  }
  h5 {
    @apply font-nunito font-semibold text-xl tracking-wide text-center;
  }
  a {
    @apply text-base;
  }
  p {
    @apply font-nunito font-normal text-base leading-8;
  }
}
